* {
    margin: 0;
    padding: 0;
    font-family: "Lilita One", cursive;
  }
  
  header {
    color: #fff;
    padding: 10px;
    border-radius: 0 0 10px 10px;
    width: 100%;
    height:120px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
  }
  img {
    font-size: 0.5rem;
  }
  
  .logo {
    display: flex;
    align-items: center;
    position: absolute;
    left: 10px;
  }
  
  .logo-image {
    width: 100px;
    z-index: 1;
  }
  
  .logo-title {
    position: absolute;
    left: 72px;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 1.5rem;
    width: 200px;
  }
  
  .title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .title h1 {
    font-size: 2.5rem;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0;
  }
  .title p {
    font-size: 1.2rem;
    font-weight: 300;
    margin: 0;
  }
  .stats {
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    right: 12%;
  }
  
  main {
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
    background: #1b181f;
    height: calc(100vh - 147px);
  }
  .leftSide {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
  }
  .error {
    color: red;
    font-size: 1.5rem;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0;
    margin-bottom: 10px;
    text-align: center;
  }
  .players {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
  }
  .players > p {
    margin: 5px 0 5px;
  }
  .player {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: rgb(10, 0, 0);
    width: 200px;
    height: 250px;
    margin: 10px 0 0 0;
    background: rgb(78, 63, 255);
    background: linear-gradient(
      85deg,
      rgba(78, 63, 255, 1) 0%,
      rgba(63, 86, 255, 1) 100%
    );
    border-radius: 10px;
    cursor: pointer;
  }
  .player p,
  .player h2,
  .player h1 {
    font-family: "Teko", sans-serif;
  }
  .player-info-image {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }
  .player-info-image img {
    width: 30px;
  }
  .player-info {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .player-info-image > img {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid white;
    flex: 2;
  }
  .player-name {
    font-size: 1.5rem;
  }
  .player hr {
    width: 80%;
    margin: 10px 0;
    border: 0;
    height: 2px;
    border-radius: 2px;
    background-color: rgb(29, 29, 29);
    margin: 0 0 5px;
  }
  .player-rating {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 10px;
    align-items: center;
  }
  
  .rightSide {
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .lineup {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  
  .lineup-players h2 {
    font-size: 1.5rem;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0;
    margin-bottom: 10px;
    text-align: center;
  }
  .defender {
    color: rgb(5, 116, 5);
  }
  .midfielder {
    color: rgb(1, 1, 181);
  }
  .forward {
    color: rgb(193, 0, 0);
  }
  .lineup-players p {
    font-size: 0.6rem;
  }
  
  .avatar-player {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
    padding: 5px;
    border-radius: 10px;
    position: relative;
  }
  .cross {
    position: absolute;
    width: 10px;
    top: 3%;
    right: 3%;
    cursor: pointer;
  }
  .avatar-player:hover {
    background: rgb(29, 29, 29);
  }
  
  .avatar-player-image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
  }
  .avatar-player .player-info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .avatar-player .player-info p:first-child {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }
  .avatar-player .player-info p:first-child img {
    width: 20px;
  }
  
  .lineup-photo {
    position: relative;
  }
  .lineup-photo img {
    width: 350px;
  }
  
  .lineup-player {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-style: italic;
  }
  /* .lineup-player.hover > img {
    border: 0;
  } */
  .lineup-player1.hover > img {
    box-shadow: 0 0 60px 10px #000000;
  }
  .lineup-player2.hover > img,
  .lineup-player3.hover > img,
  .lineup-player4.hover > img,
  .lineup-player5.hover > img {
    box-shadow: 0 0 60px 10px green;
  }
  .lineup-player6.hover > img,
  .lineup-player7.hover > img,
  .lineup-player8.hover > img {
    box-shadow: 0 0 60px 10px #8673f3;
  }
  .lineup-player9.hover > img,
  .lineup-player11.hover > img,
  .lineup-player10.hover > img {
    box-shadow: 0 0 60px 10px #df6b6b;
  }
  
  /*GK**/
  .lineup-player1 {
    top: 83%;
    left: 41%;
  }
  /*RB*/
  .lineup-player2 {
    top: 63%;
    left: 72%;
  }
  /*LB*/
  .lineup-player3 {
    top: 63%;
    left: 10%;
  }
  /*CB*/
  .lineup-player4 {
    top: 70%;
    left: 30%;
  }
  /*CB*/
  .lineup-player5 {
    top: 70%;
    left: 52%;
  }
  /*CDM*/
  .lineup-player6 {
    top: 50%;
    left: 41%;
  }
  /*CM*/
  .lineup-player7 {
    top: 35%;
    left: 27%;
  }
  /*CM*/
  .lineup-player8 {
    top: 35%;
    left: 55%;
  }
  /*RW*/
  .lineup-player9 {
    top: 20%;
    left: 72%;
  }
  /*LW*/
  .lineup-player11 {
    top: 20%;
    left: 10%;
  }
  /*ST*/
  .lineup-player10 {
    top: 10%;
    left: 41%;
  }
  .lineup-player img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid black;
    position: relative;
    top: 5px;
  }
  .lineup-player p {
    background-color: rgb(5, 4, 4);
    transform: skew(-10deg);
    font-size: 0.6rem;
    padding: 0 5px 0;
    width: 50px;
    text-align: center;
  }
  
  .popupFantasy {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
  }
  .popupFantasy-content {
    width: 400px;
    height: 400px;
    background-color: rgba(14, 1, 1, 0.8); /* Adjust alpha (0.8 for example) to control transparency */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    padding: 0;
    border-radius: 10px;
  }
  
  .form-add-player {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    color: rgb(236, 234, 234);
    background-color: #d3e631;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    position: relative;
  }
  
  .arrow {
    display: block;
    position: absolute;
    font-size: 1.9rem;
    background-color:#1b181f;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  .before-arrow {
    top: 5%;
    left: 5%;
  }
  /* .after-arrow {
    top: 5%;
    right: 5%;
  } */
  
  .form-add-player p {
    font-size: 1.5rem;
    text-align: center;
  }
  .positions {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    height: 150px;
  }
  
  .positions div {
    text-align: center;
    align-items: center;
    height: 30px;
    width: 150px;
    border-radius: 10px;
    padding: 5px;
    background-color:#1b181f;
    cursor: pointer;
  }
  .confirmation {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 10px;
    width: 90%;
  }
  .confirmation div {
    width: 150px;
    border-radius: 10px;
    text-align: center;
    padding: 5px;
    cursor: pointer;
  }
  .confirmation div:first-child {
    flex: 2;
    background-color:#1b181f;
  }
  .confirmation div:last-child {
    flex: 1;
    background-color: #1b181f;
  }
  .confirmation-players {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    height: 100px;
  }
  .confirmation-players div {
    width: 150px;
    background-color: #ffffff;
    border-radius: 10px;
    text-align: center;
    padding: 5px;
    cursor: pointer;
  }
  